.ui-datepicker{background-color:#ffffff;display:inline-block;vertical-align:middle;z-index:1000; padding: 0; font-family: 'Noto Sans';}
.ui-datepicker.ui-widget-content{width:100%;box-sizing:border-box;padding: 8px;border-radius: 2px;z-index: 1600 !important;max-width: 260px;display:none;margin: 3px 0;border-color: #efefef;box-shadow: rgba(100,100,100,0.2) 1px 1px 4px;}
.ui-datepicker .ui-datepicker-title{margin: 0px 0px 0 0;/* min-height:30px; */}
.ui-datepicker .ui-datepicker-title .uf-ui-datepicker-year{ width:80px !important;}
.ui-datepicker .ui-datepicker-title .uf-ui-datepicker-month{ width:60px !important;}
.ui-datepicker .ui-datepicker-title .selector{ margin:0 2px; height:30px;background-position:right -3px !important; }
.ui-datepicker .ui-datepicker-title .selector span{ line-height:27px; height:30px; text-align:left; font-weight:normal; }
.ui-datepicker .ui-datepicker-title .selector select{ width:100% !important; height:30px; opacity:0;appearance:none;}
.ui-datepicker-title{ margin:0px;}
.ui-datepicker-title select{ width:100%;}
.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next{background: no-repeat center;background-size: 24px auto;width: 26px;height: 26px;border: #eaeaea solid 1px;border-radius: 0px;box-sizing:border-box;vertical-align:middle;position:absolute;top: 0;left:auto;cursor: pointer;border-radius: 2px;}
.ui-datepicker .ui-datepicker-prev{background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});left: 0px;}
.ui-datepicker .ui-datepicker-next{background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});right: 0px;}
.ui-datepicker .ui-datepicker-prev span,
.ui-datepicker .ui-datepicker-next span{display:none;}

/* .ui-datepicker .ui-datepicker-prev.ui-state-hover,
.ui-datepicker .ui-datepicker-next.ui-state-hover{border:none;} */
.ui-datepicker .ui-widget-header{background:none;border:none;text-align: left;padding: 0 30px;}

.ui-datepicker-calendar thead th,
.ui-datepicker-calendar tbody td{text-align:center;font-size: 12px;white-space: nowrap;border: #eaeaea solid 1px;}
.ui-datepicker-calendar tbody td a{text-align:center;padding: 3px 5px;color:#666666;}

.ui-datepicker.week .ui-state-default.ui-state-active,
.ui-datepicker.week .ui-state-default.ui-state-active:hover{color: #666666;background-color: #ffffff;}
.ui-datepicker.week td.activeDays{background: #deefff !important;border-radius: 0px;}
.ui-datepicker.week td.activeDays>*{background-color: transparent !important;border-color: transparent;}

.ui-datepicker table{margin-bottom:0px;margin-top: 8px;border: #d9d9d9 solid 1px;height: 207px;table-layout: fixed;}
.ui-datepicker td span,
.ui-datepicker td a{text-align:center;width: 26px;height: 26px;margin: auto;border-radius: 100px;line-height: 19px;border:1px solid transparent;}
.ui-datepicker td{padding: 1px 0px;}
.ui-datepicker td:first-child a{color:#ec151e}
.ui-datepicker .ui-widget-content .ui-state-hover{background-color: #ffffff;border-width: 0;}
.ui-datepicker .ui-state-highlight,
.ui-datepicker .ui-widget-content .ui-state-highlight,
.ui-datepicker .ui-widget-header .ui-state-highlight{background-color: #ffffff;font-weight: normal;color: #181818;border: #0052bb solid 1px;}
.ui-datepicker th{font-size: 12px;border-color: #eaeaea;}
.ui-datepicker th:first-child{color:#ec151e}
.ui-datepicker-trigger{ cursor:pointer;}
.ui-datepicker .ui-datepicker-title select{margin: 0 6px 0 0;height: 26px;width: 70px;padding: 0 20px 0 10px;background: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat right 0px top 50% transparent;background-size: 24px auto;-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;border: #eaeaea solid 1px;border-radius: 2px;color: #888; font-family: 'Noto Sans';}
.ui-datepicker .ui-datepicker-title select.ui-datepicker-month{width: 60px;}
.ui-datepicker .ui-state-default,
.ui-datepicker .ui-widget-content .ui-state-default,
.ui-datepicker .ui-widget-header .ui-state-default{background-image:none;background-color:none;background-color:#FFF;border-color:#fff;color:#666666;}
.ui-datepicker .ui-datepicker-today .ui-state-highlight{color:#333333;border-color: #0052bb;}
.ui-datepicker .ui-state-default.ui-state-active,
.ui-datepicker .ui-state-default.ui-state-active:hover{color:#ffffff;background-color: #0052bb;}

.month_year_datepicker .ui-datepicker-calendar{display:none;}
.month_year_datepicker .ui-datepicker-close{border: #ddd solid 1px !important;margin: 7px 0px 0px 0px !important;font-weight: normal;}
.month_year_datepicker button.ui-datepicker-current{border: #ddd solid 1px !important;margin: 7px 0px 0px 0px !important;}
