/*** 셀렉트 박스 ***/
.select{ display:inline-block; vertical-align:middle; position:relative;}
.select > select{ width: 100%; border: 1px solid var(--p-clr) ;user-select:none;-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;outline-color: var(--p-clr); font-size: 14px;color: var(--black);max-width: 100%; border-radius: 5px; padding: 0 35px 0 10px; }
.select > select[disabled]{opacity: 1.7;-webkit-text-fill-color: var(--gray); background: var(--gray-white) ;border-color: var(--gray);}
.select::after { content: 'arrow_drop_down';  font-family: 'Material Icons'; position: absolute; top: 50%; right: 10px; transform: translateY(-50%); pointer-events: none;}
select::-ms-expand {display:none;}

/*** 텍스트 area ***/
/*** 인풋박스***/
.textarea{display: block; position:relative;}
.input>input,

.textarea > textarea{width: 100%;display:inline-block; border: 1px solid var(--gray-white); padding: 6px 10px ;background-image:none;-webkit-appearance:none;resize:none;outline:none;vertical-align: middle;-webkit-border-radius: 2px;border-radius: 2px;font-size: 12px; line-height: 160%; border-radius: 5px;color: var(--black);}

.input{display:inline-block;vertical-align:middle;position:relative; }
.input.del>input,
.input.sch>input{width: 100%; padding-right: 28px;}
.input .del::after { content: 'close'; font-family: 'Material Icons'; position: absolute; top: 50%; right: 8px; font-size: 20px; transform: translateY(-50%); color: var(--gray); cursor: pointer;}
.input.sch .btnSch::after { content: 'search'; font-family: 'Material Icons'; position: absolute; top: 50%; right: 8px; font-size: 20px; transform: translateY(-50%); color: var(--gray);}

.input>input[readonly],
.textarea>textarea[readonly]{background-color: var(--gray-white);}
.input>input[disabled],
.textarea>textarea[disabled]{background-color: var(--gray);}

/*** 인푹 캘린더***/
.ui-input-date{ position:relative; width:140px; }
.ui-input-date input{width:100%;}
.ui-input-date input.datepicker,
.ui-input-date input.datepicker:hover{padding-right: 30px; background-size: 24px auto;color: #999;cursor: pointer;border-radius: 5px;}
.ui-input-date input.datepicker + .ui-datepicker-trigger{width: 24px;height: 24px;font-size:0px;vertical-align:middle;outline:none;position: absolute;right: 2px;top: 50%; transform: translateY(-50%); border: none;background: none;}
