r\n.btn-dis-bder     {background: var(--black-darker);    color: var(--white);}\r\n.btn-dis-gler     {background: var(--gray-lighter);    color: var(--white);}\r\n.btn-dis-gl       {background: var(--gray-light);      color: var(--white);}\r\n.btn-dis-g        {background: var(--gray);            color: var(--white);}\r\n.btn-dis-gd       {background: var(--gray-dark);       color: var(--white);}\r\n.btn-dis-gder     {background: var(--gray-darker);     color: var(--white);}\r\n\r\n/*** elevation ***/\r\n.elev01 {box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.15), 0 1px 2px 0 rgba(0, 0, 0, 0.30);}\r\n.elev02 {box-shadow: 0 2px 6px 2px rgba(0, 0, 0, 0.25), 0 1px 2px 0 rgba(0, 0, 0, 0.30);}\r\n.elev03 {box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.30), 0 4px 8px 3px rgba(0, 0, 0, 0.15);}\r\n.elev04 {box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.30), 0 6px 10px 4px rgba(0, 0, 0, 0.15);}\r\n.elev05 {box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.30), 0 8px 12px 6px rgba(0, 0, 0, 0.15);}\r\n.elev06 {box-shadow: -1.5px 4px 4px 0 rgba(0, 0, 0, 0.35);}/*icon-shadow*/\r\n\r\n/*** Background ***/\r\n.bg-p { background: var(--p-clr);}\r\n.bg-a { background: var(--alert);}\r\n\r\n:root{\r\n  /*** 색상 ***/\r\n  --p-clr :        #019945;\r\n  --alert :        #e84a4b;\r\n  --alert10 :      #fdeded;\r\n  --black-darker : #001300;\r\n  --black-dark:    #1a2b1a;\r\n  --black:         #334233;\r\n  --black-light:   #4d5a4d;\r\n  --black-lighter: #667166;\r\n  --gray-darker:   #808980;\r\n  --gray-dark:     #99a199;\r\n  --gray:          #b3b8b3;\r\n  --gray-light:    #ccd0cc;\r\n  --gray-lighter:  #e6e7e6;\r\n  --white:         #ffffff;\r\n  --gray-white:    #f4f4f4;\r\n\r\n  --p-clr-drk10 :#018a3e;\r\n  --p-clr-drk20 :#017a37;\r\n  --p-clr-drk30 :#016b30;\r\n  --p-clr-drk40 :#015c29;\r\n  --p-clr-drk50 :#014d23;\r\n  --p-clr-drk60 :#003d1c;\r\n  --p-clr-drk70 :#002e15;\r\n  --p-clr-drk80 :#001f0e;\r\n  --p-clr-drk90 :#000f07;\r\n\r\n  --p-clr-lgt10 :#1aa358;\r\n  --p-clr-lgt20 :#34ad6a;\r\n  --p-clr-lgt30 :#4db87d;\r\n  --p-clr-lgt40 :#67c28f;\r\n  --p-clr-lgt50 :#80cca2;\r\n  --p-clr-lgt60 :#99d6b5;\r\n  --p-clr-lgt70 :#b3e0c7;\r\n  --p-clr-lgt80 :#e6f5ec;\r\n  --p-clr-lgt90 :#e6f5ec;\r\n\r\n  /*** 너비 ***/\r\n  \r\n}\r\n\r\n /* 프라이머리 컬러 베리데이션 */\r\n.p-clr-drk10 {background: var(--p-clr-drk10); color: var(--white);}\r\n.p-clr-drk20 {background: var(--p-clr-drk20); color: var(--white);}\r\n.p-clr-drk30 {background: var(--p-clr-drk30); color: var(--white);}\r\n.p-clr-drk40 {background: var(--p-clr-drk40); color: var(--white);}\r\n.p-clr-drk50 {background: var(--p-clr-drk50); color: var(--white);}\r\n.p-clr-drk60 {background: var(--p-clr-drk60); color: var(--white);}\r\n.p-clr-drk70 {background: var(--p-clr-drk70); color: var(--white);}\r\n.p-clr-drk80 {background: var(--p-clr-drk80); color: var(--white);}\r\n.p-clr-drk90 {background: var(--p-clr-drk90); color: var(--white);}\r\n\r\n.p-clr-lgt10 {background: var(--p-clr-lgt10);}\r\n.p-clr-lgt20 {background: var(--p-clr-lgt20);}\r\n.p-clr-lgt30 {background: var(--p-clr-lgt30);}\r\n.p-clr-lgt40 {background: var(--p-clr-lgt40);}\r\n.p-clr-lgt50 {background: var(--p-clr-lgt50);}\r\n.p-clr-lgt60 {background: var(--p-clr-lgt60);}\r\n.p-clr-lgt70 {background: var(--p-clr-lgt70);}\r\n.p-clr-lgt80 {background: var(--p-clr-lgt80);}\r\n.p-clr-lgt90 {background: var(--p-clr-lgt90);}\r\n\r\n[class*=\"-rd-bx\"] {display:flex; width: 100%}\r\n[class*=\"-rd-bx\"] input    {display:none;}\r\n[class*=\"-rd-bx\"] input:checked + label  {background: var(--p-clr); font-weight: bold; color: var(--white); cursor: default;}\r\n[class*=\"-rd-bx\"] label    {display:flex; align-items: center; justify-content: center; width: 50%; background: var(--white); color: var(--black-darker); border: 1px solid var(--gray-lighter); cursor: pointer;}\r\n[class*=\"-rd-bx\"] label.on {display:flex; align-items: center; justify-content: center; width: 50%; border: 1px solid var(--p-clr); background: var(--p-clr);color: var(--white); cursor: default; font-weight: 600;}\r\n[class*=\"-rd-bx\"] input:disabled + label { background: var(--gray-white); border: 1px solid var(--gray-lighter); cursor: default; pointer-events: none; }\r\n\r\n.one-rd-bx label { border-radius: 25px; width: 100%;}\r\n\r\n.tw-rd-bx label:nth-child(2) {border-radius:25px 0 0 25px;}\r\n.tw-rd-bx label:nth-child(4) {border-radius:0 25px 25px 0;}\r\n\r\n/*** two-radio-box ***/\r\n.thr-rd-bx label:nth-child(2) {border-radius:25px 0 0 25px;}\r\n.thr-rd-bx label:nth-child(6) {border-radius:0 25px 25px 0;}\r\n\r\n.fr-rd-bx label:nth-child(2) {border-radius:25px 0 0 25px;}\r\n.fr-rd-bx label:nth-child(8) {border-radius:0 25px 25px 0;}\r\n\r\n.fv-rd-bx label:nth-child(2) {border-radius:25px 0 0 25px;}\r\n.fv-rd-bx label:nth-child(10){border-radius:0 25px 25px 0;}\r\n\r\n\r\n/* 그라데이션 박스 */\r\n.lbl-opt {display: flex; align-items: center; justify-content: flex-start; position: relative; width: 32%; height: 75px; border-radius: 10px; position: relative; padding: 16px 20px; box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.15), 0px 1px 2px 0px rgba(0, 0, 0, 0.30); border: 1px solid var(--white); background: var(--white); margin: 0 0 16px 0; cursor: pointer;}\r\n.lbl-opt img:nth-child(1) {width: 63px; height: auto;}\r\n.lbl-opt .lbl-miri {display: inline-block; width: 60px; height: 36px; background: url(../img/common/img_ico_miri.png) no-repeat; background-size: contain; background-position: center;}\r\n.lbl-opt .lbl-txt {font-size: 24px; font-weight: 800; padding-left: 8px; font-family: 'Archivo' !important;}\r\n.lbl-opt .lbl-img {display: none; position: absolute; right: 24px; width: 36px; height: 36px; background: url(../img/common/ico_check_w.png) no-repeat; }\r\n.lbl-opt-ip {display: none;}\r\n\r\n.lbl-opt-ip:disabled + label {background: var(--gray-light); cursor: default; pointer-events: none;}\r\n\r\n.lbl-opt-ip:checked + label {background: linear-gradient(45deg, #9cddb2, #019945); font-weight: bold; color: var(--white); cursor: default;}\r\n\r\n/*** font-color ***/\r\n.fc-bder      {color: #001300 !important;}\r\n.fc-bd        {color: #1a2b1a !important;}\r\n.fc-b         {color: #334233 !important;}\r\n.fc-bl        {color: #4d5a4d !important;}\r\n.fc-bler      {color: #667166 !important;}\r\n.fc-gder      {color: #808980 !important;}\r\n.fc-gd        {color: #99a199 !important;}\r\n.fc-g         {color: #b3b8b3 !important;}\r\n.fc-gl        {color: #ccd0cc !important;}\r\n.fc-gler      {color: #e6e7e6 !important;}\r\n.fc-w         {color: #ffffff !important;}\r\n.fc-gw        {color: #f4f4f4 !important;}\r\n.fc-ylw       {color: #ffb400;}\r\n.fc-ylw:focus {color: var(--p-clr);}\r\n\r\n/* 기간설정 */\r\n.range {display: flex; align-items: center; justify-content: center;}\r\n.range span{ padding: 0 10px;}\r\n.range input[type=range]{width:60%; height: 5px; box-shadow:inset 2px 2px 4px 0 rgba(0,0,0, 0.2); background: #e6e7e6; border-radius: 0.5rem; -webkit-appearance: none; }\r\n.range input[type=range]::-webkit-slider-thumb {background: var(--p-clr); cursor: pointer; border: 1px solid #fff; box-shadow: 2px 2px 4px 0 rgba(0,0,0, .3); height: 16px; width: 45px; border-radius: 10px; -webkit-appearance: none; }\r\n\r\n/* 라디오박스 */\r\n.rds01[type=\"radio\"]                 {width: 20px; height: 20px; border: 1px solid #b3b8b3; border-radius: 50%; background-color: var(--white); position: relative; cursor: pointer; -webkit-appearance: none; -moz-appearance: none; appearance: none; }\r\n.rds01[type=\"radio\"]:checked         {border: 1px solid var(--p-clr); background-color: var(--white);}\r\n.rds01[type=\"radio\"]:disabled        {background-color: var(--gray-white); cursor: default;}\r\n.rds01[type=\"radio\"]:checked::before {content: ''; display: block; width: 12px; height: 12px; background-color: var(--p-clr); border-radius: 50%;  position: absolute;  top: 50%;  left: 50%;  transform: translate(-50%, -50%);}\r\n.rds01[type=\"radio\"]:checked:disabled {border: 1px solid var(--p-clr); background-color: var(--gray); }\r\n.rds01[type=\"radio\"]:checked:disabled::before { content: ''; display: block; width: 12px; height: 12px; background-color: var(--p-clr); border-radius: 50%; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);}\r\n.rds01[type=\"radio\"] + span          {margin: 0 30px 0 6px; cursor: pointer; }\r\n.rds01[type=\"radio\"]:disabled + span {margin: 0 30px 0 6px; cursor: default; }\r\n\r\n.rds02[type=\"radio\"]                  {appearance: none; border: 1px solid #b3b8b3; width: 20px; height: 20px; border-radius: 50%;}\r\n.rds02[type=\"radio\"]:checked          {width: 20px; height: 20px; border: 0.5em solid var(--p-clr); border-radius: 50%;}\r\n.rds02[type=\"radio\"]:disabled         {background-color: var(--gray-white);}\r\n.rds02[type=\"radio\"]:checked:disabled {background-color: var(--gray);}\r\n.rds02[type=\"radio\"] + span           {margin: 0 30px 0 6px; cursor: pointer;}\r\n.rds02[type=\"radio\"]:disabled + span  {margin: 0 30px 0 6px; cursor: default;}\r\n\r\n/* 체크박스 */\r\n.chkbx01 input[type=\"checkbox\"]                  {appearance: none; border: 1px solid #b3b8b3; width: 20px; height: 20px; border-radius:5px; position: relative; cursor: pointer;}\r\n.chkbx01 input[type=\"checkbox\"]:checked          {background: var(--p-clr); border: 1px solid var(--p-clr);}\r\n.chkbx01 input[type=\"checkbox\"]:disabled         {background: var(--gray-white); border: 1px solid #b3b8b3; cursor: default;}\r\n.chkbx01 input[type=\"checkbox\"]:checked:disabled {background: var(--gray); border: 1px solid #b3b8b3;}\r\n.chkbx01 input[type=\"checkbox\"]:checked::before  {content: '\\e5ca'; font-family: 'Material Icons'; position: absolute; color: var(--white); top: 50%; left: 50%; transform: translate(-50%, -50%); }\r\n.chkbx01 input[type=\"checkbox\"] + span           {vertical-align: middle; padding: 0 30px 0 6px; cursor: pointer;}\r\n.chkbx01 input[type=\"checkbox\"]:disabled + span  {vertical-align: middle; padding: 0 30px 0 6px; cursor: default;}\r\n\r\n.chkbx02 input[ty