___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";
.gp {
  position: relative;
  font-family: "Noto Sans", Arial, "Malgun Gothic", "맑은 고딕", Helvetica, sans-serif, "Apple Gothic",  "돋음", "Dotum";
  font-size: 12px;
  color: #333;
}

.gp:after {
  content: "";
  clear: both;
  display: block;
}

.gp .expand {
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: end;
  width: 200px;
}

.gp_left {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 250px;
  background: #f9f9f9;
  border-right: solid 1px #e1e1e1;
  overflow-y: auto;
}

.gp_left > ul {
  padding: 0 20px;
}

.gp_left > ul > li {
  position: relative;
  border-bottom: solid 1px #e1e1e1;
  padding-bottom: 10px;
  padding-left: 10px;
  margin-top: 10px;
}

.gp_left > ul > li:last-child {
  margin-bottom: 50px;
}

.gp_left > ul > li:before {
  position: Absolute;
  top: 9px;
  left: 10px;
  content: "";
  width: 4px;
  height: 4px;
  background: #4e547c;
  border-radius: 100%;
}

.gp_left > ul > li > a {
  font-weight: bold;
  padding-left: 10px;
}

.gp_left > ul ul {
  padding-left: 20px;
}

.gp_left > ul ul a.active {
  color: var(--p-clr);
  font-weight: bolder;
}

.gp_left > ul > li > ul > li {
  padding: 5px 0;
}

.gp_right {
  padding: 20px 20px 100px 270px;
}

.gp_right .element {
  margin-bottom: 100px;
}

.gp_right .element.hide {
  margin-bottom: 0;
}

.gp_right .element .elements {
  position: relative;
  width: 1320px;
  padding: 20px;
}

.gp_right .element .elements:after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  z-index: -1;
  border: solid 1px #ddd;
  border-radius: 6px;
}

.gp_right .element.hide .elements {
  display: none;
}

.gp_right h2 {
  position: relative;
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  margin: 0 0 20px;
  text-align: left;
}

.gp_right h2:before {
  position: absolute;
  top: 50%;
  left: 0;
  content: "";
  z-index: 0;
  width: 100%;
  height: 1px;
  background: #ccc;
}

.gp_right h2 i {
  position: absolute;
  top: 50%;
  width: 25px;
  height: 25px;
  background: #fff;
  font-size: 25px;
  vertical-align: middle;
  transform: translateY(-50%);
}

.gp_right h2 span {
  position: absolute;
  top: 0;
  left: 30px;
  background: #fff;
  cursor: pointer;
  font-style: italic;
  padding: 0 10px 0 0;
}

.gp_right span.use,
.gp_right span.preview,
.gp_right span.html,
.gp_right span.script {
  display: block;
  background: #ddd;
  border-radius: 3px;
  color: #333;
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 20px;
  padding: 5px 10px;
}

.gp_right span.use {
  background: #f3d2d2;
  border: solid 1px #f0b7b7;
}

.gp_right span.preview {
  background: #f5f5f5;
  border: solid 1px #d8d8d8;
}

.gp_right span.html {
  background: #d4f3fd;
  border: solid 1px #94bbeb;
  margin-top: 30px;
}

.gp_right span.script {
  background: #f8f8dd;
  border: solid 1px #d3d952;
  margin-top: 30px;
}

.gp_right [id^=highlighter] {
  margin: 0 !important;
  padding: 0 !important;
}

.gp_right .desc li {
  list-style-type: disc;
  margin-left: 30px;
}

.gp_right .desc li .blue {
  color: #3377c0;
}

.gp_right .desc li .red {
  color: #ff0000;
}

.gp_right .desc + .preview {
  margin-top: 30px;
}

